.todo-input-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 20px;
  background-color: #fff;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  padding: 10px 20px;
  width: 80%;
  max-width: 500px;
  margin: 0 auto;
}

.todo-input-container input[type="text"] {
  border: none;
  outline: none;
  font-size: 16px;
  padding: 10px;
  flex: 1;
}

.todo-input-container button {
  background: linear-gradient(to bottom right, #3b92ff, #1163c9);
  color: #fff;
  border: none;
  border-radius: 20px;
  font-size: 16px;
  padding: 10px 20px;
  cursor: pointer;
  margin-left: 10px;
}

.todo-input-container button:hover {
  background: linear-gradient(to bottom right, #3487d1, #0e55a9);
}

/* Styling for todo list */
.todo-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.todo-list li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #ccc;
  padding: 10px 0;
}

.todo-text {
  font-size: 18px;
}

.todo-buttons {
  display: flex;
}

.todo-buttons button {
  background: none;
  border: none;
  color: #3b92ff;
  font-size: 16px;
  cursor: pointer;
  margin-left: 10px;
  transition: color 0.2s ease-in-out;
}

.todo-buttons button:hover {
  color: #1163c9;
}
